import React from 'react';
import {Pressable, StyleSheet, Text, View} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {THEME} from "../../../theme";
import {reloadBDayPrice, setBDayManualOff, setBDayStatus} from "../../../store/actions/post";

export const ConditionsPromokod = ({typeReturn = 1})=>{

    const dispatch = useDispatch();
    const promokodeErrorInfo = useSelector(state => state.post.promokodeErrorInfo);
    const BDayActive = useSelector(state => state.post.BDay.BDay);
    const BDayManualOff = useSelector(state => state.post.BDayManualOff);
    const canDisableBDay = promokodeErrorInfo.showIconError && promokodeErrorInfo?.errorProduct?.code === "bday_conflict" && BDayActive === true && BDayManualOff === false;
    const infoProductMessage = promokodeErrorInfo?.infoProduct?.status ? promokodeErrorInfo.infoProduct.message : null;

    const handleDisableBDay = ()=>{
        dispatch(setBDayManualOff(true));
        dispatch(reloadBDayPrice(0));
        dispatch(setBDayStatus({BDay:false}));
    };

    const getCHeckSum = (type = 1)=>{
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorSum.countSum>0){
            switch (type) {
                case 2:
                    return (<Text style={styles.BoldText}>{promokodeErrorInfo.errorSum.countSum} </Text>);
                case 1:
                default:
                    return (<Text style={styles.TextInfoMini}>       : <Text style={styles.BoldText}>{promokodeErrorInfo.errorSum.countSum} </Text></Text>);
            }
        }else{
            return false;
        }
    };


    const getCheckDiscountOtherProduct = ()=>{
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorProduct.status === true){
            return (
                <View>
                    <Text style={styles.TextInfoMini}>{promokodeErrorInfo.errorProduct.message}</Text>
                    {canDisableBDay ?
                        <Pressable style={styles.DisableBDayBtn} onPress={handleDisableBDay}>
                            <Text style={styles.DisableBDayBtnText}>    </Text>
                        </Pressable>
                        : false}
                </View>
            );
        }else if(infoProductMessage){
            return (
                <Text style={styles.TextInfoMini}>{infoProductMessage}</Text>
            );
        }else{
            return false;

        }
    };


    let ReturnData = false;

    switch (typeReturn) {
        case 1:
            ReturnData = getCHeckSum(1);
            break;
        case 2:
            ReturnData = getCheckDiscountOtherProduct();
            break;
        case 3:
            ReturnData = getCHeckSum(2);
            break;
        default: ReturnData = false;
    }


    return (ReturnData);
};


const styles = StyleSheet.create({
    BoldText:{
    fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    TextInfoMini:{
        backgroundColor:THEME.INFO_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:5,
        marginVertical: 10
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    TextInfoMiniError:{
        backgroundColor:THEME.DANGER_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    titleChildBlock:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16,
        marginBottom:10
    },
    DisableBDayBtn:{
        backgroundColor:"#ffffff",
        borderColor:THEME.INFO_COLOR,
        borderWidth:1,
        borderRadius:10,
        paddingVertical:8,
        paddingHorizontal:10,
        marginTop:10
    },
    DisableBDayBtnText:{
        color:THEME.INFO_COLOR,
        fontSize:14,
        textAlign:"center",
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700, zIndex:99999
    }
});
