const DEFAULT_SITE_NAME = Settings.SettingsApp.ActiveSiteName;

const normalizeKey = (value) => {
    if(!value || typeof value !== 'string'){ return null; }
    const trimmed = value.trim();
    if(trimmed.length <= 0){ return null; }
    const direct = ListSite[trimmed] ? trimmed : null;
    if(direct){ return direct; }
    const lower = trimmed.toLowerCase();
    const found = Object.keys(ListSite).find((key)=> key.toLowerCase() === lower);
    return found || null;
};

const getThemeFromUrl = () => {
    if(typeof window === 'undefined'){ return null; }
    try {
        const params = new URLSearchParams(window.location.search);
        const queryTheme = params.get('theme') || params.get('site');
        const themeByQuery = normalizeKey(queryTheme);
        if(themeByQuery){ return themeByQuery; }

        const pathSegments = window.location.pathname.split('/').filter(Boolean);
        const themeByPath = pathSegments.map(normalizeKey).find(Boolean);
        if(themeByPath){ return themeByPath; }
    }catch (e){
        console.log('Theme detection error', e);
    }
    return null;
};

const ActiveSiteName = getThemeFromUrl() || DEFAULT_SITE_NAME;

export const ActiveSiteSettings = {
    "URL":ListSite[ActiveSiteName].URL,
    "THEME":ListSite[ActiveSiteName].THEME,
    "THEMENAME":ActiveSiteName,
    "secretKey": ListSite[ActiveSiteName].secretKey,
    "idGroupVK": ListSite[ActiveSiteName]?.idGroupVK || null
};

export const getActiveSiteName = () => ActiveSiteName;
