import axios from 'axios';
import { API_KEY, URL_API, CITY_DATA } from './store/types';
import { useDispatch, useSelector } from 'react-redux';
import React from "react";
import {GetPriceProductBasketNumber, GetTypePriceProductBasket} from "./components/Basket/getPriceProductBasket";
import {NumberFormatPrice} from "./templateComponents/NumberFormat/NumberFormat";
import {getTypeDiscountPromoInfo} from "./components/SystemFunction/getTypeDiscountPromoInfo";

export class PromokodeClass extends React.Component {
    constructor(props){
        super(props);
    }

    static getSalePromoOK(Product,PromoProductId) {
        let go_sale_ok = true;
        // if (typeof PromoData == 'undefined') PromoData = {};

        //    .      .
        if(Product.id_Product*1 === PromoProductId*1){
            go_sale_ok=true
        }else {
        GetTypePriceProductBasket(Product) ? go_sale_ok = false : go_sale_ok = true;
        }

        return go_sale_ok;
    }


    //   
    static getPriceFromPromokode(
        Product, //    (        
        SumReturn = 0, //    
        type_price, //    ( )
        countProductBasket = 0, //-     
        countAllProductInCart = 0, //-          
        sumRemaining = 0, //    ,         
        Type_Product, //  type_product 0,1,2,3  all
        PromoData = {}, //        
        countProductListBasket = 0, //-      
        idProduct = 0,
        return_default = false, // id    ,
        modifyList = [], //  ,
        sumProductDiscount = 0, //    ,    0,    
        BDay=false, //  
        needCount= 0 //  -   
    ) {
        let countReturnNext = 1;
        let countProductinCartLocal = 0;

        let conditionProductPromokode = this.createConditionProductPromokode(true,0,{id_Product:0,type_product:0,id_Category:0},0);

        type_price = GetPriceProductBasketNumber(Product,countReturnNext,false, modifyList);
        let type_priceOld = GetPriceProductBasketNumber(Product,countReturnNext,false, modifyList,true);

        if(PromoData !== null){
            if(PromoData?.promokodeInfo?.addition_discount === true || (PromoData.productInfo.id_product*1 === Product.id_Product*1)){
                return_default = false;
            }
        }

        if(return_default || PromoData === null){
            SumReturn = SumReturn + countProductBasket * type_price;
            return {
                "SumResult":parseFloat(SumReturn.toFixed(0)),
                "countProductListBasket":countProductListBasket,
                "countAllProductInCart":0,
                "sumRemaining":0,
                "sumProductDiscount":sumProductDiscount,
                "countProductinCartLocal":countProductinCartLocal,
                "conditionProductPromokode":conditionProductPromokode,
                "discountOneProduct":0
            };
        }

        let sales = 0;
        let proc_Sum = 0;

        if(type_price<type_priceOld && PromoData?.promokodeInfo?.addition_discount === false && (PromoData?.productInfo.id_product*1 !== Product.id_Product*1)){
            if(PromoData?.promokodeInfo?.promo_total === true){
                proc_Sum = 0;
            }else {
                proc_Sum = 0;
            }
        }else {
            proc_Sum = getTypeDiscountPromoInfo(PromoData.promokodeInfo.sale_type,PromoData.promokodeInfo.sale,type_priceOld);
        }

        let go_sale_ok = true;

            let totalSumCheck = 0

            if(sumRemaining > 0){
                if(totalSumCheck <= (PromoData.promokodeInfo.sale)*1) {
                    proc_Sum = sumRemaining;
                    sales = sumRemaining;
                    sumRemaining = 0;
                    }

            }

            if(!return_default) {
                if (PromoData?.promokodeInfo?.addition_discount === true) {
                   go_sale_ok = true;
                } else {
                    go_sale_ok = this.getSalePromoOK(Product,PromoData.productInfo.id_product);
                }
            }else {
                go_sale_ok = false;
            }


            /////
                //    
                const checkCategoryMatch = (Product, PromoData)=>{
            return Number(Product.id_Category) === Number(PromoData.productInfo.id_category);
        };

        //      
        const checkQuantityLimits = (countProductBasket, PromoData, countAllProductInCart, countProductListBasket)=>{
        const maxCount = Number(PromoData.productInfo.count_product);
            if (maxCount === 0) return true; //  

            //        
            if (countAllProductInCart >= maxCount || countProductListBasket >= maxCount) {
                return false; //  
            }

            // ,      
            if (countProductBasket <= maxCount) {
                return true; //   
            }

            return false; //    
        };

        //    
        const applyDiscountForProducts = (SumReturn, proc_Sum, type_priceOld, countProducts, allowRemaining = true)=>{
        const productCount = Number(countProducts); //   
        SumReturn += productCount * NumberFormatPrice((type_priceOld - proc_Sum), 0, '.', '');
            const result = this.handleNegativeSum(SumReturn, proc_Sum, { allowRemaining });

            return {
                SumReturn: result.SumReturn,
                proc_Sum: result.proc_Sum,
                sumRemaining: result.sumRemaining || 0,
                discountApplied: productCount * NumberFormatPrice((type_priceOld - proc_Sum), 0, '.', '')
            };
        };

        //      
        const checkNeedCount = (PromoData, needCount) => {
    const activeCount = Math.min(Number(PromoData?.dop_settings?.count_product_active), Number(needCount));
    return activeCount >= Number(PromoData?.dop_settings?.count_product_active);
        };

        //     
        const checkCountProductActive = (PromoData) => {
            return Number(PromoData?.dop_settings?.count_product_active) === 0 || PromoData?.dop_settings?.count_product_active === false;
        };

        //    
        const checkProductMatch = (idProduct, PromoData, Type_Product) => {
            return Number(idProduct) === Number(PromoData.productInfo.id_product) && this.isTypeMatching(Type_Product, PromoData);
        };



            /////
            if(PromoData?.promokodeInfo?.promo_total !== true) {
                //     "sale_category"
                if (PromoData.promokodeInfo.type === "sale_category") {
                    if (this.isBirthdayApplicable(BDay, PromoData) && go_sale_ok) {
                        if (checkCategoryMatch(Product, PromoData)) {
                            if (countAllProductInCart >= Number(PromoData.productInfo.count_product)) {
                                                SumReturn += countProductBasket * type_price; //  
                            } else if (checkQuantityLimits(countProductBasket, PromoData, countAllProductInCart, countProductListBasket)) {
                                const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, countProductBasket, true);
                                SumReturn = discountResult.SumReturn;
                                proc_Sum = discountResult.proc_Sum;
                                sumRemaining = discountResult.sumRemaining;
                                sumProductDiscount += discountResult.discountApplied;

                                countProductinCartLocal += countProductBasket;
                                conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                                countProductListBasket++;
                                countAllProductInCart++;
                            } else {
                                let difference = countProductBasket - Number(PromoData.productInfo.count_product);
                                const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, PromoData.productInfo.count_product, true);
                                SumReturn = discountResult.SumReturn;
                                proc_Sum = discountResult.proc_Sum;
                                sumRemaining = discountResult.sumRemaining;

                                SumReturn += difference * type_price;
                                sumProductDiscount += discountResult.discountApplied;
                            }
                        } else {
                            SumReturn += countProductBasket * type_price; //   
                        }
                    } else {
                        SumReturn += countProductBasket * type_price; //   
                    }
                }

                //     "sale_product"
                if (PromoData.promokodeInfo.type === "sale_product") {
                    if (this.isBirthdayApplicable(BDay, PromoData) && go_sale_ok) {
                        if (checkProductMatch(idProduct, PromoData, Type_Product)) {
                            if (checkNeedCount(PromoData, needCount)) {
                                if (countProductBasket <= Number(PromoData.productInfo.count_product) || Number(PromoData.productInfo.count_product) === 0) {
                                    const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, countProductBasket, false);
                                    SumReturn = discountResult.SumReturn;
                                    proc_Sum = discountResult.proc_Sum;
                                                        sumRemaining = 0; // ,     
                                    sumProductDiscount += discountResult.discountApplied;
                                if (sumProductDiscount < 0) sumProductDiscount = 0;
                                    countProductinCartLocal += countProductBasket;
                                    conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                                } else {
                                    let difference = countProductBasket - Number(PromoData.productInfo.count_product);
                                    const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, PromoData.productInfo.count_product, false);
                                    SumReturn = discountResult.SumReturn;
                                    proc_Sum = discountResult.proc_Sum;
                                    sumRemaining = 0;

                                    SumReturn += difference * type_price;
                                    sumProductDiscount += discountResult.discountApplied;
                                    countProductinCartLocal += Number(PromoData.productInfo.count_product);
                                    conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                                }
                            } else if (checkCountProductActive(PromoData)) {
                                const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, countProductBasket, false);
                                SumReturn = discountResult.SumReturn;
                                proc_Sum = discountResult.proc_Sum;
                                sumRemaining = 0;
                                sumProductDiscount += discountResult.discountApplied;
                                countProductinCartLocal += countProductBasket;
                                conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                            } else {
                                SumReturn += countProductBasket * type_price; //    
                            }
                        } else {
                            SumReturn += countProductBasket * type_price; //   
                        }
                    } else {
                        SumReturn += countProductBasket * type_price; //   
                    }
                }

                //  
                if (PromoData.promokodeInfo.type === "sale_all") {
                    if (this.isBirthdayApplicable(BDay, PromoData) && go_sale_ok) {
                        if (countAllProductInCart >= Number(PromoData.productInfo.count_product) || countProductListBasket >= Number(PromoData.productInfo.count_product)) {
                            SumReturn += countProductBasket * type_price; //  
                        } else if (checkQuantityLimits(countProductBasket, PromoData, countAllProductInCart, countProductListBasket)) {
                            const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, countProductBasket, true);
                            SumReturn = discountResult.SumReturn;
                            proc_Sum = discountResult.proc_Sum;
                            sumRemaining = discountResult.sumRemaining;

                            sumProductDiscount += discountResult.discountApplied;
                            if (sumProductDiscount < 0) sumProductDiscount = 0;

                            conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                            countProductinCartLocal += countProductBasket;
                                countProductListBasket++;
                                countAllProductInCart++;
                            } else {
                                let difference = countProductBasket - Number(PromoData.productInfo.count_product);
                                const discountResult = applyDiscountForProducts(SumReturn, proc_Sum, type_priceOld, PromoData.productInfo.count_product, true);
                                SumReturn = discountResult.SumReturn;
                                proc_Sum = discountResult.proc_Sum;
                                sumRemaining = discountResult.sumRemaining;

                                SumReturn += difference * type_price;
                                sumProductDiscount += discountResult.discountApplied;
                                countProductinCartLocal += Number(PromoData.productInfo.count_product);
                                conditionProductPromokode = this.createConditionProductPromokode(true, countProductinCartLocal, Product, proc_Sum);
                            }
                    } else {
                        SumReturn += countProductBasket * type_price; //   
                    }
                }

            }else{
                SumReturn = SumReturn + countProductBasket * type_price;
            }

        if(sumRemaining <0){
            // sumProductDiscount = 0;
        }

        return {
            "SumResult":parseFloat(SumReturn.toFixed(0)),
            "countProductListBasket":countProductListBasket,
            "countAllProductInCart":countAllProductInCart,
            "sumRemaining":sumRemaining,
            "sumProductDiscount":sumProductDiscount,
            "countProductinCartLocal":countProductinCartLocal,
            "conditionProductPromokode":conditionProductPromokode,
            "promo_total":PromoData?.promokodeInfo?.promo_total
        };
    }

    //     conditionProductPromokode
    static createConditionProductPromokode(status, count, Product, proc_Sum) {
        return {
            "status": status,
            "count": count,
            "id": Product.id_Product,
            "type_id": Product.type_product,
            "category_id": Product.id_Category,
            "discountOneProduct": proc_Sum
        };
    }

    //      
    static isBirthdayApplicable(BDay, PromoData) {
        return (BDay === true && PromoData.promokodeInfo.addition_birthday === true) || BDay === false;
    }

    //      
    static isTypeMatching(Type_Product, PromoData) {
        return Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all";
    }

    //       
    static handleNegativeSum(SumReturn, proc_Sum, options = {}) {
        const { allowRemaining = false, resetSum = true } = options;
        if (SumReturn < 0) {
            proc_Sum += SumReturn;
            const sumRemaining = allowRemaining ? Math.abs(SumReturn) : 0;
            if (resetSum) SumReturn = 0;
            return { SumReturn, proc_Sum, sumRemaining };
        }
        return { SumReturn, proc_Sum, sumRemaining: null };
    }

}
