import React from "react";
import {StyleSheet, View, Text, TouchableOpacity} from "react-native";
import {MaterialIcons} from "@expo/vector-icons";
import {useDispatch, useSelector} from "react-redux";
import {setPersonCount} from "../../../store/actions/post";
import {THEME} from "../../../theme";

const MIN_PERSON_COUNT = 1;
const MAX_PERSON_COUNT = 20;

export const PersonCount = () => {
    const dispatch = useDispatch();
    const personCount = useSelector(state => state.post.personCount);

    const updateCount = (delta) => {
        let nextCount = (personCount || MIN_PERSON_COUNT) + delta;
        if (nextCount < MIN_PERSON_COUNT) nextCount = MIN_PERSON_COUNT;
        if (nextCount > MAX_PERSON_COUNT) nextCount = MAX_PERSON_COUNT;
        dispatch(setPersonCount(nextCount));
    };

    return (
        <View style={styles.BoxWhiteBlock}>
            <View style={styles.counterRow}>
                <TouchableOpacity
                    onPress={() => updateCount(-1)}
                    style={styles.counterButton}
                    activeOpacity={0.7}
                >
                    <MaterialIcons name="remove" size={20} color="#ffffff" />
                </TouchableOpacity>
                <Text style={styles.counterValue}>{personCount || MIN_PERSON_COUNT}</Text>
                <TouchableOpacity
                    onPress={() => updateCount(1)}
                    style={styles.counterButton}
                    activeOpacity={0.7}
                >
                    <MaterialIcons name="add" size={20} color="#ffffff" />
                </TouchableOpacity>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    BoxWhiteBlock: {
        backgroundColor: "#ffffff",
        borderRadius: 10,
        paddingHorizontal: 10,
        paddingVertical: 8,
    },
    counterRow: {
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "#ffffff",
        borderRadius: 10,
        paddingVertical: 6
    },
    counterButton: {
        width: 32,
        height: 32,
        borderRadius: 8,
        backgroundColor: THEME.MAIN_COLOR,
        alignItems: "center",
        justifyContent: "center"
    },
    counterValue: {
        minWidth: 44,
        textAlign: "center",
        fontSize: 16,
        fontFamily: "open-bold, segoe ui, Arial",
        fontWeight: "700",
        color: "#222"
    },
});
