import React, {useEffect} from "react";
import {StyleSheet, View, Platform} from "react-native";
import {MaterialIcons} from "@expo/vector-icons";
import {Select} from "../../../templateComponents/Select/Select";
import {useDispatch, useSelector} from "react-redux";
import {setChangeAmount} from "../../../store/actions/post";

export const ChangeAmount = () => {
    const dispatch = useDispatch();

    const SelectOption = () => {
        const changeAmount = useSelector(state => state.post.changeAmount);

        const handleChangeAmount = (amount) => {
            dispatch(setChangeAmount(amount));
        };

        const denominationOptions = [
            {title: " ", value: null, disabled: true}, // 
            {title: "50 ", value: 50},
            {title: "100 ", value: 100},
            {title: "200 ", value: 200},
            {title: "500 ", value: 500},
            {title: "1000 ", value: 1000},
            {title: "2000 ", value: 2000},
            {title: "5000 ", value: 5000},
        ];

        useEffect(() => {
            if (changeAmount === null) {
                handleChangeAmount(null); //   
            }
        }, []);

        return (
            <Select
                defaultValue={changeAmount}
                fontSize={12}
                item={denominationOptions}
                keys="ChangeAmount"
                onChange={handleChangeAmount}
                placeholder="   " //  Select  `placeholder`
            />
        );
    };

    const ChangeAmountSelector =
        <View
            style={{
                flexDirection: "row",
                justifyContent: "space-between",
                alignItems: "center",
                width: "100%"
            }}
        >
            <View style={{flexDirection: "row", justifyContent: 'flex-start', alignItems: "center", width: "95%"}}>
                {SelectOption()}
            </View>
            <View style={styles.arrowRight}>
                <MaterialIcons name="keyboard-arrow-right" size={20} color="black" />
            </View>
        </View>;

    return (
        <View style={[styles.BoxWhiteBlock, styles.shadow]}>
            {ChangeAmountSelector}
        </View>
    );
};

const styles = StyleSheet.create({
    BoxWhiteBlock: {
        backgroundColor: "#ffffff",
        borderRadius: 10,
        paddingHorizontal: 10,
        paddingVertical: 10,
    },
    arrowRight: {
        width: "5%",
        marginLeft: 0,
        paddingTop: Platform.OS === 'ios' ? 0 : 2
    },
    shadow: {
        borderWidth: 0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.22,
        shadowRadius: 40,
        elevation: 3,
        marginBottom: 0
    },
});
