import {CheckModifyProductAccordance} from "../CheckModifyFunction/CheckModifyProductAccordance";

export const GetAllPriceBasketPreorderToppingSum = (
    PreorderCategoryIdObject = [],
    basket = [],
    allProducts = [],
    promoDiscounts = null
)=>{

    if(allProducts === null){
        return 0;
    }

    let AllSum = 0;


    const getOkCategory = (idCategory)=>{
        let OK = false;
        Object.keys(PreorderCategoryIdObject).forEach((idCatOk,i)=>{
            if(idCatOk === idCategory){ OK = true; }
        });
        return OK;
    };

    const getProductData = (categoryId, productId, typeId) => {
        const productList = allProducts?.[categoryId]?.ProductList?.[productId];
        if(!Array.isArray(productList)){ return null; }
        return productList.find((dataProduct) => dataProduct.type_product == typeId) || null;
    };

    const getPromoPrice = (productId, typeId, modifyList = [])=>{
        if(!promoDiscounts?.arrayProductDiscount){ return null; }

        const promoItem = promoDiscounts.arrayProductDiscount.find((item)=>
            item.id_product*1 === productId*1 &&
            item.type_product*1 === typeId*1 &&
            CheckModifyProductAccordance(item.modifyListProduct ?? [], modifyList ?? [])
        );

        if(!promoItem){ return null; }

        const price = Number(promoItem.sumPriceSale ?? promoItem.sumPriceDef);
        return Number.isFinite(price) ? price : null;
    };

    basket.forEach((productBasket,num)=>{
        productBasket.type.forEach((typeProducts,numbs)=>{
            if(!(typeProducts.categoryId in allProducts)){ return; }
            if(!getOkCategory(typeProducts.categoryId)){ return; }

            const product = getProductData(typeProducts.categoryId, productBasket.id, typeProducts.type_id);
            if(!product){ return; }

            const modifySum = (typeProducts?.modifyList || []).reduce((sum, modify)=> sum + (Number(modify.price) || 0),0);
            const basePrice = product.price_discount !== false
                ? Number(product?.price_discount?.new_price) || 0
                : Number(product?.price) || 0;
            const count = Number(typeProducts.count) || 0;

            const promoPrice = getPromoPrice(productBasket.id, typeProducts.type_id, typeProducts?.modifyList);
            const SumProduct = promoPrice ?? ((basePrice + modifySum) * count);

            AllSum += SumProduct;
        });
    });

    return AllSum;
};
